

















import {Component, Prop, Vue} from "vue-property-decorator"
import {ISubscription} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import CardPlans from "@/components/CardPlans.vue"
import {IPayment} from "@chaosinsight/yogipeople-portalclient"
import {ISessionTicket} from "@chaosinsight/yogipeople-portalclient"
import {IPaymentMethod} from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"

@Component({ components: { CardPlans } })
export default class HeroText2 extends Vue {
	private sessionTickets: number = 0
	private isTrial: boolean | null = null
	private isSubscribed: ISubscription | null = null
	private tempInfo: string = ""
	private planInfo: string = ""
	private modal: ISubscription | null = null
	private SubscriptionCode: string = ""
	private systemTime!: string
	private timeDelta: number = 0
	private diffInTime: number = 0
	private diffInDays: number = 0
	private renewalDate: number = 0
	private sessionText: string = ""
	private hasBooking: boolean | null = null
	private isCancelled: boolean | null = null
	private hasUpcomingSession: boolean | null = null
	private openPlans: boolean | null = null
	private getTicket: string | null = ""
	private increaseTicket: string | null = ""
	private linkText: string = "Choose Subscription Plan"
	private listenInterval = 1500
	private listenHandle: number | null = null
	private savedCards: IPaymentMethod[] | null = null
	@Prop()
	private activeItem!: string
	private testing!: ISessionTicket[]
	private mounted(): void {
		Portal.client.call.sessionTickets.get()
		.response
		.then(res => {
			this.sessionTickets = res.length
			this.increaseTicket = window.localStorage.getItem("increaseticket")
			window.localStorage.removeItem("increaseticket")
			if (this.increaseTicket === "planvise")
				if (this.SubscriptionCode === "subscription-a" || this.SubscriptionCode === "subscription-a-us"
				|| this.SubscriptionCode === "subscription-a-uk" || this.SubscriptionCode === "subscription-a-sek" || this.SubscriptionCode === "subscription-a-nor")
					this.sessionTickets = this.sessionTickets + 2
				else if (this.SubscriptionCode === "subscription-b" || this.SubscriptionCode === "subscription-b-us"
					|| this.SubscriptionCode === "subscription-b-uk" || this.SubscriptionCode === "subscription-b-sek" || this.SubscriptionCode === "subscription-b-nor")
					this.sessionTickets = this.sessionTickets + 4
				else if (this.SubscriptionCode === "subscription-c" || this.SubscriptionCode === "subscription-c-us"
					|| this.SubscriptionCode === "subscription-c-uk" || this.SubscriptionCode === "subscription-c-sek" || this.SubscriptionCode === "subscription-c-nor")
					this.sessionTickets = this.sessionTickets + 6
				else this.sessionTickets = this.sessionTickets + 0
			this.updateSessionTicket()
			if (this.sessionTickets === 1)
				this.sessionText = "Session"
			else
				this.sessionText = "Sessions"
		})
	}
	private created(): void {
		this.hasPlan()
		this.getSystemTime()
		this.daysRemaining()
		Portal.client.call.booking.get()
		.response
		.then(result => {
			if (result.length > 0) {
					this.hasBooking = true
					this.isCancelled = true
				} else {
					this.hasBooking = false
					this.isCancelled = true }
			Portal.client.call.student.follows()
			.response
			.then(res => {
				if (res.length > 0)
				this.hasUpcomingSession = true
					else
					this.hasUpcomingSession = false
			})
		})
		}
		private destroyed(): void {
		if (this.listenHandle !== null)
			clearTimeout(this.listenHandle)
	}
	private checkPayment(): void {
		Portal.client.call.payments.paymentMethodsGet("card")
					.response
					.then(result => {
						this.savedCards = [].slice.call(result)
						if (this.savedCards!.length === 0) {
						window.localStorage.setItem("routeplaninfo", "yes")
						this.$router.push("/card") } else this.$emit("openPlan")
						})
	}
	private updateSessionTicket(): void {
		this.listenHandle = window.setTimeout(() =>
		Portal.client.call.sessionTickets.get()
			.response
			.then(res => {
				this.sessionTickets = res.length
				this.updateSessionTicket()
			}), this.listenInterval)
	}
	private getSystemTime(): void {
		Portal.client.call.system.Time()
		.response
		.then(res => {
			this.systemTime = res.Time
		})
}
	private daysRemaining(): void {
		Portal.client.call.subscriptions.get()
		.response
		.then(res => {
			this.timeDelta = new Date(res.RenewalDate).getTime() - Date.now()
			this.diffInTime = this.timeDelta / (1000 * 60 * 60 * 24)
			this.diffInDays = Math.round(this.diffInTime)
			})
		.catch(reason => {
		})
		}
	private hasPlan(): void {
		Portal.client.call.subscriptions.get()
		.response
		.then(res => {
			this.isSubscribed = res
			this.SubscriptionCode = res.SubscriptionCode
			this.isTrial = res.IsTrial
			if (this.isSubscribed === null || this.isSubscribed === undefined) {
				this.planInfo = "No Plan"
				this.linkText = "Choose Subscription Plan" } else if (this.isSubscribed !== null)
							if (this.isTrial)
									if (this.SubscriptionCode === "subscription-a" || this.SubscriptionCode === "subscription-a-us"
									|| this.SubscriptionCode === "subscription-a-uk" || this.SubscriptionCode === "subscription-a-sek" || this.SubscriptionCode === "subscription-a-nor")
											this.planInfo = "Rise (Free Trial)"
									else if (this.SubscriptionCode === "subscription-b" || this.SubscriptionCode === "subscription-b-us"
									|| this.SubscriptionCode === "subscription-b-uk" || this.SubscriptionCode === "subscription-b-sek" || this.SubscriptionCode === "subscription-b-nor")
											this.planInfo = "Steady (Free Trial)"
									else this.planInfo = "Guru (Free Trial)"
							else if (this.isSubscribed !== null)
							if (this.isTrial === false)
											if (this.SubscriptionCode === "subscription-a" || this.SubscriptionCode === "subscription-a-us"
									|| this.SubscriptionCode === "subscription-a-uk" || this.SubscriptionCode === "subscription-a-sek" || this.SubscriptionCode === "subscription-a-nor")
												this.planInfo = "Rise"
											else if (this.SubscriptionCode === "subscription-b" || this.SubscriptionCode === "subscription-b-us"
									|| this.SubscriptionCode === "subscription-b-uk" || this.SubscriptionCode === "subscription-b-sek" || this.SubscriptionCode === "subscription-b-nor")
												this.planInfo = "Steady"
											else this.planInfo = "Guru"
							else  this.planInfo = "No Plan"
		})
		.catch(reason => {
		})
	}
	}
